.portfolio__wrap {
  margin-top: 50px;
}
.cards {
  margin-bottom: 156px;
}

.card {
  width: 900px;
  height: 600px;
  margin: 0 auto;
  cursor: pointer;
}
.card-wrp {
  position: relative;
  margin-bottom: 32px;
}

.card__img {
  width: 100%;
  height: auto;
  display: block;
}
.card__modal {
  position: absolute;
  background-image: linear-gradient(#ef8c8b, #f0d789);
  opacity: 0;
  width: 900px;
  height: 470px;
  top: 1px;
}
.card__icon {
  position: absolute;
  opacity: 0;
  width: 100%;
  text-align: center;
  z-index: 20;
}
.card:hover .card__modal {
  opacity: 0.8;
  -webkit-transform: translate(-10px, -10px);
  -ms-transform: translate(-10px, -10px);
  transform: translate(-10px, -10px);
  -webkit-box-shadow: 10px 10px 10px #95e1d3;
  box-shadow: 10px 10px 10px #95e1d3;
}
.card:hover .card__icon {
  opacity: 1;
}
.card__icon:hover {
  opacity: 1;
}
.icon__btn {
  --s: 50px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background: transparent;
  margin: 0 3px 10px 0;
  height: var(--s);
  width: var(--s);
  border: 1px solid #fff;
  cursor: pointer;
  vertical-align: top;
  background: #fce38a;
}

.icon__btn:hover {
  background: #46bfa9;
}

.card__text {
  font-size: 18px;
  font-family: "Roboto";
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}
.card__title {
  font-size: 14px;
  font-family: "Montserrat";
  color: rgb(51, 51, 51);
}

.card__subtitle {
  font-size: 15px;
  font-family: "Roboto";
  color: rgb(153, 153, 153);
  font-style: italic;
  line-height: 1.6;
}

.img {
  width: 900px;
  height: 480px;
}
.portfolio__title {
  text-align: center;
  font-size: 36px;
}
.card__title {
  font-size: 24px;
  text-align: center;
}
.github {
  display: block;
  text-decoration: none;
  color: black;
  font-size: 50px;
  text-align: center;
}
