.hero {
  padding-top: 50px;
  padding-bottom: 155px;
  background-image: url("../../img//bg2.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  height: 100vh;
}
.hero-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 105px;
  color: #000;
  font-size: 30px;
  font-weight: bold;
}

.social-link {
  margin-left: 20px;
}
.social-link:first-child {
  margin-left: 0;
}
.hero-content {
  display: flex;
  font-size: 50px;
  color: #000;
}
.tel {
  text-decoration: none;
  color: #000;
}
