.quote__wrap {
  background: #41969c;
  color: #fff;
}
.blockquote {
  margin: 0;
}
.quote {
  padding: 50px 0 10px 0;
  font-style: italic;
  font-weight: 300;
  line-height: 1.1;
  font-size: 3.0625em;
  text-align: center;
  margin: 0 auto;
}
.quote:before {
  content: "“";
}
.quote:after {
  content: "”";
}
.quote__footer {
  display: block;
  color: #94d2d7;
  text-align: right;
  margin-top: 10px;
  padding-bottom: 20px;
}
